<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process126"></a>

<p><br></p>
<p><span class="rvts17">大逃亡（</span><span class="rvts50">The Escape</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">下篇</span><span class="rvts50">[</span><span class="rvts17">主线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:559,Y:273</span><span class="rvts21">，地点：港口</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">逃离陆地</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1043.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">需要到达地图东北角的港口，并与亚历山大的部队发生一场恶战，才能完成该章的任务。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



